import React, { Component } from "react";
import './database.css'

class PrintDB extends Component {
  constructor(props) {
    super(props);
    this.state = {
        db_elements: props.db_elements
    }
  }

  render() {
    if (this.state.db_elements.length != 0) {
        return(
            <table align="center">
                <tbody>
                    <tr className="table-header">
                        <td>Nickname</td>
                        <td>Phone</td>
                        <td>Date</td>
                    </tr>
                    {this.state.db_elements.map((element) => {
                        let date = new Date(element.date) 
                        date = date.getDate() + '.' + date.getMonth() + '.' + date.getFullYear()
                        return (
                            <tr key={element.id}>
                                <td>{element.name}</td>
                                <td>{element.phone}</td>
                                <td>{date}</td>
                            </tr>
                        )
                    })}
                </tbody>
            </table>
        )
    }
    else
        return(<></>)
  }
}

export default PrintDB;
