import React, { Component } from "react"
import PrintDB from "./PrintDB"
import "../sorting/sorting.css"
import "./database.css"

class Database extends Component {
    constructor(props) {
        super(props)
        this.state = {
            db_elements: []
        }
        this.print_db = null;

        this.printDB = this.printDB.bind(this)
    }

    async printDB() {
        let obj = {
            headers: {
                "Content-Type":"application/json"
            }
        }
        
        let response = await fetch("http://localhost:5000/db/print", obj)
        let result = await response.json()

        let tmp = []
        for (let key in result) {
            tmp.push(result[key])
        }

        this.print_db = (
            <PrintDB db_elements={tmp} />
        )

        this.setState({
            db_elements: tmp
        }, () => {
            console.log(this.state.db_elements)
        })
    }

    render() {
        return(
            <>
                <div className='main'>
                    База данных дней рождений
                </div>
                <form method='post' className="send-form">
                    <input type='text' name='name' id='name' className="inp" placeholder="Nickname" required/>
                    <input type='tel' name='phone' id='name' className="inp" pattern="\+7[0-9]{10}" placeholder="+7" required />
                    <input type='date' name='date' id='date' className="inp" required />
                    <input type='submit' value='Добавить в БД' className="btn-send"/>
                </form>
                <div className="api">
                <button onClick={this.printDB}>
                    Показать ДБ
                </button>
                </div>
                {this.print_db}
            </>
        )
    }
}

export default Database;